'use strict';

import Base from "./common/Base";
import html from '../../template/sideMenu.html';
import {isJson, isUndefined, isArray, isFunction, isEqual, isString} from "./common/util";

const menuLevel = "menuLevel";
//
const menuTextClass = "group_title",
	liClass = `.hasarr`,
	openClass = "open",
	activeClass = "active",
	subMenuClass = "sub-menu",
	iconClass = "group_icon",
	aFilter = ".syind_menu_ul li a"
;

const keyMap = {
	menuId: "menuId",
	menuLevel: "menuLevel",
	isOpen: "isOpen",
	menuText: "menuText",
	parentId: "parentId",
	href: "href",
	children: "children",
	iconClass: "icon"
	// children
// createTime
// creator
// flag
// flag_name
// icon
// isopen
// level
// menuLevel
// menu_id
// menu_name
// menu_name_en
// open_type
// orderNo
// parentId
// updateTime
// updator
// url
}

// 解析json
const parseMenuJson = json => {
	let group = {
			root: []
		},
		arr = [];
	//
	for (let item of json.data) {
		let parentId = item[keyMap.parentId];
		// 添加根节点
		let key = parentId === null ? "root" : parentId;
		//
		if (!group[key]) {
			group[key] = [];
		}
		// 设置根节点menuLevel
		if (key === "root") {
			item[menuLevel] = 0;
		}
		group[key].push(item);
	}
	//
	for (let item of group.root) {
		addJsonChildren(item, group, arr);
	}
	//
	return arr;
}

// 处理json子节点
const addJsonChildren = (currentNode, group, arr) => {
	arr.push(currentNode);
	let currentId = currentNode[keyMap.menuId];
	let children = group[currentId];
	if (children) {
		let newMenuLevel = currentNode[menuLevel] + 1;
		currentNode.children = [];
		for (let item of children) {
			item[menuLevel] = newMenuLevel;
			addJsonChildren(item, group, currentNode.children);
		}
	}
}

// 解析模板
const parseTemplate = () => {
	let $html = $(html);
	let $mainA = $html.find(`${liClass}>a:first`).clone();
	let $subA = $html.find(`${liClass}>ul a:first`).clone();
	$mainA.find(".group_title").empty();
	$subA.find(".group_title").empty();
	let $mainUl = $html.clone().empty();
	let $subUl = $html.find(`ul.${subMenuClass}:first`).clone().empty();
	let $li = $html.find(`${liClass}:first`).clone().empty();
	//
	return {
		$html, $mainA, $subA, $mainUl, $subUl, $li
	}
}

// 生成dom
const createDom = (jsonArr, $holder, _root) => {
	let {$html, $mainA, $subA, $mainUl, $subUl, $li} = _root._data.template;
	//
	let $cUl = jsonArr[0].menuLevel === 0 ? $mainUl.clone() : $subUl.clone();
	$cUl.appendTo($holder);
	// 创建dom
	for (let item of jsonArr) {
		let {menuLevel} = item;
		let children = item[keyMap.children];
		let menuText = item[keyMap.menuText];
		let isOpen = item[keyMap.isOpen];
		let href = item[keyMap.href];
		let icon = item[keyMap.iconClass];
		//
		let $cA = menuLevel === 0 ? $mainA.clone() : $subA.clone(),
			$cLi = $li.clone();
		//
		$cA.find(`.${menuTextClass}`).html(menuText);
		icon && $cA.find(`.${iconClass} i`).removeClass().addClass(icon);
		$cA.appendTo($cLi);
		$cLi.appendTo($cUl);
		isOpen !== "0" ? $cLi.addClass(openClass) : "";
		//
		if (isArray(children)) { // 有子节点
			$cA.attr("href", "javascript:void(0)");
			createDom(children, $cLi, _root);
		} else { // 无子节点
			$cA.attr("href", href || "javascript:void(0)");
			$cLi.removeClass("hasarr");
			$cLi.removeClass(openClass);
		}
	}
}

// bindEvent
const bindEvent = ($dom,isAccordion) => {
	const delay = 200;
	//点击li
	$dom.on("click", aFilter, function () {
		let $thisLi = $(this).parent('li');
		let $submenutemp = $(this).siblings(`.${subMenuClass}`);
		let menunum = $submenutemp.length;
		if (menunum > 0) {
			let $submenu = $submenutemp.eq(0);
			if ($thisLi.hasClass(openClass)) {
				//收起当前菜单
				$submenu.slideUp(delay, function () {
					$thisLi.removeClass(openClass);
				});
			} else {
				//收起其他同级菜单，展开当前菜单
				$submenu.slideDown(delay, function () {
					$thisLi.addClass(openClass);
				});
				//isAccordion，默认为true开启手风琴效果，如果设为false，则可以同时展开多个菜单
				if(isAccordion){
					$thisLi.siblings().children(`.${subMenuClass}`).slideUp(delay, function () {
						$thisLi.siblings().removeClass(openClass);
					});
				}
			}
		} else {
			$(aFilter).removeClass(activeClass);
			$(this).addClass(activeClass);
		}
	});
}

//
class SideMenu extends Base {
	constructor(json) {
		super();
		//
		let {el,isAccordion=true} = json,
			$dom = $("<div class='syind_menu'></div>").appendTo(el),
			jsonArr = [];
		// 解析模板
		this._data = {
			template: parseTemplate(),
			customData: json,
			$dom
		}
		// key map
		$.extend(keyMap, json.keyMap);
		// 解析json
		if (isJson(json) && isArray(json.data)) {
			// 解析json
			jsonArr = parseMenuJson(json, this);
		} else {
			throw new TypeError("navigation.location()参数类型错误，参数必须{el,data}形式");
		}
		// createDom
		createDom(jsonArr, $dom, this);
		// bindEvent
		bindEvent(el,isAccordion);
	}

	active(target) {
		if (isString(target)) {
			let {el,isAccordion=true} = this._data.customData;
			let $a = el.find(`a[href='${target}']`);
			//
			if ($a.length === 1) {
				$(aFilter).removeClass(activeClass);
				$a.addClass(activeClass);
				//isAccordion，默认为true开启手风琴效果，如果设为false，则可以同时展开多个菜单
				if(isAccordion){
					el.find(`.${openClass}`).removeClass(openClass);
					el.find(`.${subMenuClass}`).hide();
				}
				/*el.find(`.${openClass}`).removeClass(openClass);
				el.find(`.${subMenuClass}`).hide();*/
				el.find("ul").has($a).show();
				el.find("li").has($a).addClass(openClass);
			} else {
				throw new Error(`active()参数 ${target} 链接不唯一`);
			}
		}
	}

	get $dom() {
		return this._data.$dom;
	}
}

export default SideMenu;
