'use strict';

import Breadcrumb from "./Breadcrumb";
import SideMenu from "./SideMenu";

const sideMenu = json => {
	let instance = new SideMenu(json);
	return instance;
};

const breadcrumb = json => {
	let instance = new Breadcrumb(json);
	return instance;
};

export {sideMenu, breadcrumb};
